import React, { useState, useEffect, useRef } from 'react';
import TodoList from './TodoList';

export default function Todos() {
  const [todos, setTodos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [item, setItem] = useState('');
  const API_URL = 'https://abhility-fakedb.glitch.me/todos';
  const dateSort = useRef(null);
  const alphabetSort = useRef(null);

  const fetchData = (showLoader) => {
    setLoading(showLoader);
    fetch(API_URL)
      .then((res) => res.json())
      .then((data) => {
        setLoading(false);
        data = data.map((item) => {
          return {
            ...item,
            loading: false,
            name: item.name.toLowerCase(),
            date: new Date(item.date),
          };
        });
        data = data.reverse();
        Array.isArray(data) ? setTodos(data) : setTodos([]);
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
      });
  };

  useEffect(() => {
    fetchData(true);
    addedDateSort();
  }, []);

  const handleChange = (event) => {
    const { value } = event.target;
    setItem(value);
  };

  const addTodo = (event) => {
    event.preventDefault();
    setLoading(true);
    const todo = {
      name: item,
      date: Date.now(),
      done: false,
    };

    setItem('');

    fetch(API_URL, {
      method: 'POST',
      body: JSON.stringify(todo),
      headers: {
        'content-type': 'application/json',
      },
    })
      .then((res) => res.json())
      .then((data) => {
        fetchData(true);
        addedDateSort();
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
      });
  };

  const deleteItem = (id) => {
    let newTodos = todos.map((todo) => {
      if (todo.id === id) {
        todo.loading = true;
      }
      return todo;
    });
    setTodos(newTodos);
    fetch(`${API_URL}/${id}`, {
      method: 'DELETE',
    })
      .then((res) => res.json())
      .then((data) => {
        fetchData(false);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const alterItem = (id) => {
    let newTodos = todos.map((todo) => {
      if (todo.id === id) {
        todo.loading = true;
      }
      return todo;
    });
    setTodos(newTodos);
    let todo = todos.find((todo) => todo.id === id);
    todo = { ...todo, done: !todo.done };
    fetch(`${API_URL}/${id}`, {
      method: 'PUT',
      body: JSON.stringify(todo),
      headers: {
        'content-type': 'application/json',
      },
    })
      .then((res) => res.json())
      .then((data) => {
        fetchData(false);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const alphabeticSort = () => {
    alphabetSort.current.classList.add('white-text');
    alphabetSort.current.classList.add('teal');
    dateSort.current.classList.remove('white-text');
    dateSort.current.classList.remove('teal');

    const sortedTodos = todos.sort((first, second) =>
      first.name < second.name ? -1 : first.name > second.name ? 1 : 0
    );
    setTodos((prevTodos) => [...sortedTodos]);
  };

  const addedDateSort = () => {
    dateSort.current.classList.add('white-text');
    dateSort.current.classList.add('teal');
    alphabetSort.current.classList.remove('white-text');
    alphabetSort.current.classList.remove('teal');
    const sortedTodos = todos.sort((first, second) =>
      first.date > second.date ? -1 : first.date < second.date ? 1 : 0
    );
    setTodos((prevTodos) => [...sortedTodos]);
  };

  return (
    <div style={{ margin: '25px auto' }}>
      <form className="container row" onSubmit={addTodo}>
        <div className="input-field col s8 l8 m8">
          <input
            id="input"
            type="text"
            name="item"
            value={item}
            onChange={handleChange}
            className="validate"
            required
          />
          <label htmlFor="input">Type here</label>
        </div>
        <div className="input-field col s4 l4 m4">
          <button className="btn-floating btn-large waves-effect waves-light hoverable pulse">
            <i className="material-icons">edit</i>
          </button>
        </div>
        <div className="sorts row">
          <span className="chip col s8 l2 orange white-text sort-label">
            <i className="material-icons">sort</i>
            Sort By
          </span>
          <span className="col s0 l4" />
          <span
            className="chip col s8 l2 sort-button"
            ref={alphabetSort}
            onClick={alphabeticSort}
          >
            Alphabetically
          </span>
          <span className="col s0 l2" />
          <span
            className="chip col s8 l2 sort-button"
            ref={dateSort}
            onClick={addedDateSort}
          >
            Added Date
          </span>
        </div>
      </form>
      <div className="container row">
        <TodoList
          todos={todos}
          deleteItem={deleteItem}
          alterItem={alterItem}
          loading={loading}
        />
      </div>
    </div>
  );
}
